@import url(https://fonts.googleapis.com/css?family=Inknut+Antiqua|Work+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#cardContainer,#formContainer{

display:flex;
flex-direction: column;
align-items: center;
margin-top: 100px;

}
#cardContainer{
  margin-bottom: 100px;
}
.cardDisplay{
  border: 1px solid slateblue;
  width:700px;
  text-align: center;
}
.liveClock{
  margin-left: 10px;
}
#runBtn{
  margin-top: 20px;
}
.cardCity{
  text-align: center;
  font-family: 'Work Sans', sans-serif;
  font-size: 25px;
  color:rgb(167,109,67);

}
.localTime{
  align-items: center;
  height:40px;
  font-family: 'Work Sans', sans-serif;
  font-size: 20px;
  font-weight: bold;
  justify-content: center;
  display: flex;
  flex-direction: row;
}
.cardContent{
  font-family: 'Work Sans', sans-serif;
  font-size: 20px;
  font-weight: bold;
  margin:0;
}

